import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-cstat',
  templateUrl: './cstat.component.html',
  styleUrls: ['./cstat.component.css']
})
export class CstatComponent implements OnInit {
  tidal: number;
  Pplat: number;
  peep: number;
  ResultCstat: number;

  Cstat() {
    var a = (<HTMLInputElement>document.getElementById("tidal")).value;
    var b = (<HTMLInputElement>document.getElementById("Pplat")).value;
    var c = (<HTMLInputElement>document.getElementById("peep")).value;

    if (this.tidal != null && this.Pplat != null && this.peep != null) {
    this.ResultCstat = this.tidal / (this.Pplat - this.peep)
    document.getElementById("ResultCstat").innerHTML = this.ResultCstat.toFixed(2)+ " mL/cm H₂O";
    }
  }
  
  ngOnInit(): void {
    // this.Reset(); /* Runs Reset on page load */
  }

  Print() { /* Prints the website to PDF */
    window.print();
  }

  Reset() { /* Resets radio buttons to default*/
    this.ResultCstat = 0;
    (<HTMLInputElement>document.getElementById("tidal")).value = "";
    (<HTMLInputElement>document.getElementById("Pplat")).value = "";
    (<HTMLInputElement>document.getElementById("peep")).value = "";
    document.getElementById("ResultCstat").innerHTML = "Please fill out required fields.";
  }
}