import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';    
import { RouterModule, Routes } from '@angular/router';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { Covid19Component } from './covid19/covid19.component';
import { Curb65Component } from './curb65/curb65.component';
import { HomeComponent } from './home/home.component';
import { CstatComponent } from './cstat/cstat.component';
import { BmiBsaComponent } from './bmi-bsa/bmi-bsa.component';
import { GlasgowComaScaleComponent } from './glasgow-coma-scale/glasgow-coma-scale.component';

const appRoutes: Routes = [
  {path:'',redirectTo:'home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'covid19', component: Covid19Component },
  { path: 'curb65', component: Curb65Component },
  { path: 'cstat', component: CstatComponent },
  { path: 'bmi-bsa', component: BmiBsaComponent },
  { path: 'glasgow-coma-scale', component: GlasgowComaScaleComponent },
];

@NgModule({
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // <-- debugging purposes only
    )
  ],

  declarations: [
    AppComponent,
    Covid19Component,
    Curb65Component,
    HomeComponent,
    CstatComponent,
    BmiBsaComponent,
    GlasgowComaScaleComponent
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
